function App() {
  let res = quests.map(quest => <div style={{width: "10%", border: "solid", margin: "5px", padding: "5px"}}>
        <a href={`https://lostarkcodex.com/us/quest/${quest.quest_id}`}>{quest.quest_name}</a>
        <br />
        <div style={{overflow: "auto", height: "10em"}}>{quest.rewards.map(reward => <div>{reward}</div>)}</div>
      </div>)
  return (
    <div className="App" style={{display: "flex", "flex-wrap": "wrap"}}>
      {res}
      
    </div>
  );
}

export default App;
